.card {
  width: 340px;
  height: 340px;
  margin: 20px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}
@media (max-width: 768px) {
  .card {
    width: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.card a {
  width: 100%;
}
.card img {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}
.card .card-title {
  position: absolute;
  bottom: 5px;
  left: 10px;
  color: white;
}/*# sourceMappingURL=Card.css.map */